
#app {
  text-align: center;
}
.previewBox {
  display: inline-block;
  padding: 10px;
  width: 45vw;
  height: 90vh;
  overflow: auto;
  border: 1px solid;
  border-radius: 10px;
  text-align: left;
}
.previewBox > .title {
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  margin: 10px;
}
.actionBox {
  display: inline-block;
  padding: 10px;
  width: 45vw;
  height: 90vh;
  overflow: auto;
  border: 1px solid;
  border-radius: 10px;
  text-align: left;
}
.actionBox > .title {
  font-size: 18px;
  font-weight: bold;
  margin: 10px 0px;
}
.actionBox > textarea {
  width: 98%;
  height: 20%;
  resize: none;
}
.actionBox > .btnBox {
  text-align: right;
}
.btnBox > button {
  width: 80px;
  height: 30px;
  background-color: #4caf50; /* Green */
  border: none;
  border-radius: 8px;
  color: white;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  cursor: pointer;
}
.radioBox {
  padding: 3px 0px;
}
